export default {
	data() {
		return {
			list: [],
			addressId: ""
		};
	},
	onLoad() {},
	onShow() {
		this.addressId = uni.getStorageSync('addressId')
		uni.removeStorageSync('addressId')
		this.api.center.address.all(null, res => {
			console.log(res)
			this.list = res.data
		}, err => {

		})
	},
	methods: {

		edit(id) {
			uni.navigateTo({
				url: '../address-handle/address-handle?id=' + id
			})
		},
		deleted(id, index) {
			uni.showModal({
				title: "确定要删除吗？",
				success: (res) => {
					if (res.confirm) {
						this.api.center.address.delete({
							id
						}, res => {
							uni.showToast({
								title: res.message,
								icon: "success",
								success: () => {
									this.list.splice(index, 1)
								}
							})
						}, err => {

						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})

		},
		goBack(id) {
			uni.setStorageSync('addressId', id);
			uni.navigateBack({
				delta: 1
			})
		},
		addressAdd() {

			uni.authorize({
				scope: "scope.address",
				success: () => {
					uni.chooseAddress({
						success: (res) => {
							let model = {
								id: 0,
								sort: 0,
								sex: 1,
								is_default: 1,
							}
							model.consignee = res.userName;
							model.province = res.provinceName;
							model.city = res.cityName;
							model.area = res.countyName
							model.address = res.detailInfo;
							model.mobile = res.telNumber;
							console.log(model)
							this.api.center.address.handle(model, res => {
								uni.showToast({
									title: "操作成功",
									icon: "success"
								})
							}, err => {

							})
						}
					})
				},
				fail: () => {
					uni.navigateTo({
						url: "../address-handle/address-handle"
					})
				}
			})







		}
	}
}
